*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.header{
  display: flex;
  justify-content: center;
  background: rgb(57, 113, 235);
  height: 70px;
  text-align: center;

}
.navbar{
  display: flex;
  justify-content: space-around;
  align-items: center;
  border: 1px solid red;
  max-width: 1024px;
  width: 100%;
  
}
nav{
  display: flex;
  justify-content: center;
  height: 100%;
  border: 1px solid pink;
}
ul{
  display: flex;
  list-style: none;
  justify-content: space-around;
  align-items: center;
  width: 400px
}

nav a {
  text-decoration: none;
  color: white;
  padding: 5px;
  
}
nav a:hover {
  border-bottom: 1px solid white;
}
.submenu{
  display: none;
}
